<template>
  <div class="activeDevice">
    <header>
      <div class="content">
        <h1>激活设备</h1>
        <p>带*号的为必填项</p>
      </div>
    </header>
    <section class="info">
      <div class="info_wrapper">
        <div class="name">
          <span>设备编号</span>
          <span class="red">*</span>
        </div>
        <input type="text" />
      </div>
      <div class="info_wrapper">
        <div class="name">
          <span>投资人</span>
          <span class="red">*</span>
        </div>
        <input type="text" />
      </div>
      <div class="info_wrapper" style="border:none;">
        <div class="name">
          <span>联系电话</span>
          <span class="red">*</span>
        </div>
        <input type="text" />
      </div>
      <span class="button">激活设备</span>
    </section>
    <section class="notice">
      <h1>激活须知</h1>
      <p>1.目前激活设备仅向平台会员用户提供。</p>
      <p>2.激活设备之后一般10分钟之后即可在后台查询，具体以后台信息为准。</p>
      <p>3.如果激活过程中遇到问题，请您直接与客服联系。</p>
      <p>4.设备绑定后可以到酒店管理后台进行绑定。</p>
    </section>
  </div>
</template>

<script>
export default {};
</script>

<style scoped lang="scss">
.activeDevice {
  header {
    width: 100%;
    height: 201px;
    background: url(../../assets/images/activation_bg@2x.png) no-repeat;
    background-size: 100% 100%;
    color: #fff;
    .content {
      text-align: center;
      padding-top: 35px;
      h1 {
        font-size: 27px;
        font-family: SourceHanSansCN-Medium;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
      }
      p {
        font-size: 15px;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(255, 255, 255, 1);
        line-height: 30px;
      }
    }
  }
  .info {
    .info_wrapper {
      padding: 20px;
      border-bottom: 1px solid rgb(238, 238, 238);
      display: flex;
      justify-content: space-between;
      .red {
        color: #e62121;
      }
      input {
        border: none;
        margin-right: 90px;
      }
    }
    .button {
      background: #ff4c23;
      color: #fff;
      padding: 10px;
      box-shadow: 0px 3px 5px 0px rgba(255, 112, 54, 0.4);
      border-radius: 23px;
      display: block;
      width: 339px;
      text-align: center;
      margin: 20px auto;
    }
  }
  .notice {
    padding: 20px;
    h1 {
      font-size: 16px;
      font-family: SourceHanSansCN-Medium;
      font-weight: 600;
      color: rgba(51, 51, 51, 1);
      line-height: 20px;
    }
    p {
      font-size: 12px;
      font-family: MicrosoftYaHei;
      font-weight: 400;
      color: rgba(91, 91, 91, 1);
      line-height: 23px;
    }
  }
}
</style>



